<template>
	<div id="creditsign-record">
		<public-header title="详细记录"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="grant-list">
				<van-list v-model="records.loading" :finished="records.finished" @load="onLoad" :offset="100" :immediate-check="false" v-if="!records.empty" >
					<div class="grant-item van-hairline--bottom" v-for="item in records.data">
						<div class="item-inner">
							<div class="titles">{{item.mark}}</div>
							<div class="text"><span class="label" :class="{'label-warning': item.css == 'label-warning', 'label-success': item.css == 'label-success', 'label-info': item.css == 'label-info', 'label-primary': item.css == 'label-primary', 'label-danger': item.css == 'label-danger', 'label-default': item.css == 'label-default'}">{{item.text}}</span>{{item.addtime}}</div>
						</div>
						<div class="grant">+{{item.grant}}</div>
					</div>
				</van-list>
				<div class="common-no-con" v-else>
					<img src= "static/img/order_no_con.png" alt="" />
					<p>您还没有奖励记录</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import {getRecords} from "@/controller/funcCommon"
	export default {
		components: {
			PublicHeader,
			PublicFooter
		},
  		data() {
  			return {
  				records: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
  				},
				showPreLoading:true,
				menufooter: {}
			}
  		},
  		methods: {
			onLoad() {
				var that = this;
				getRecords({
					vue: that,
					url: 'creditsign/record',
					data: {
						menufooter: 1,
					},
					success: (res) => {
						that.menufooter = window.menufooter;
					},
				})
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#creditsign-record .grant-list{
		background-color: #fff;
	}
	#creditsign-record .grant-list .grant-item{
		position: relative;
	    display: flex;
	    padding: 10px;
	    align-items: center;
	    overflow: hidden;
	}
	#creditsign-record .grant-list .grant-item .item-inner{
	    width: 100%;
	    overflow: hidden;
	    box-sizing: border-box;
	    -webkit-box-flex: 1;
	    display: block;
	}
	#creditsign-record .grant-list .grant-item .item-inner .titles{
	    font-size: 16px;
    	color: #666;
    	line-height: 26px;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text{
		font-size: 13px;
    	color: #666;
    	line-height: 21px;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label{
		display: inline-block;
	    padding: 0 4px;
	    margin: 0 2px;
	    font-size: 12px;
	    color: #fff;
	    background-color: #4cd964;
	    line-height: 19px;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label.label-success{
		background-color: #4cd964;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label.label-primary{
		background-color: #337ab7;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label.label-info{
		background-color: #04ab02;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label.label-warning{
		background-color: #ff9326;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label.label-danger{
		background-color: #f6383a;
	}
	#creditsign-record .grant-list .grant-item .item-inner .text .label.label-default{
		background-color: #777;
	}
	#creditsign-record .grant-list .grant-item .grant{
	    vertical-align: middle;
	    margin-right: 10px;
	    color: #ff2d4b;
	    font-size: 17px;
	}
	#creditsign-record .grant-list .common-no-con{
	    position: absolute;
	    top: 32%;
	    left: 0;
	    width: 100%;
	    height: 200px;
	    text-align: center;
	    color: #768491;
	    font-size: 16px;
	}
	#creditsign-record .grant-list .common-no-con img{
	    max-width: 25%;
	    vertical-align: middle;
	    border: none;
	}
	#creditsign-record .grant-list .common-no-con p{
		margin:  20px 0;
		line-height: 26px;
		font-size: 17px;
	}
</style>


